import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { scrollToSection } from 'src/helpers';
import './Header.css'

export const Header = () => {
  const navigate = useNavigate();
  const [clickMenu, setClickMenu] = useState(false);

  useEffect(() => {
    if (clickMenu) document.body.style.overflow = 'hidden';
    else document.body.style.overflow = 'auto';
  }, [clickMenu]);

  const onClickLogo = () => {
    navigate('/');
    scrollToSection('up', -80);
  }

  return (
    <header className='header'>
      <div className="header__wrapper">
        <div className='header__navigation'>
          <a onClick={() => scrollToSection('about')}> </a>
          <a onClick={() => scrollToSection('prices')}></a>
          <a onClick={() => scrollToSection('faq')} ></a>
        </div>
        <div className="header__logo" onClick={onClickLogo}>
          <p className='logo__name'> </p>
          <p className='logo__position'>, </p>
        </div>
        <div className='header__navigation'>
          <a onClick={() => scrollToSection('qualification')}></a>
          <a onClick={() => scrollToSection('blog')} ></a>
          <a onClick={() => scrollToSection('contacts')}></a>
        </div>
        {/* <MenuIcon className='header__menu' onClick={() => setClickMenu(true)} /> */}
      </div>
      {clickMenu &&
        <div>SlideBar</div>
        // <SlideBar clickMenu={clickMenu} setClickMenu={setClickMenu} />
      }
    </header>
  )
}
